<template>
	<div>
		<home v-show="page_num == 0"></home>
		<pics v-show="page_num == 1"></pics>
		<summer v-show="page_num == 2"></summer>
	</div>
</template>

<script>
	import bus from '../event-bus.js';
	import home from './home';
	import pics from './pics';
	import summer from './summer';

	export default {
		components: {
			home, pics, summer
		},

		data () {
			return {
				page_num: 0
			}
		},

		mounted() {
			bus.$on('set_page_num', this.set_page_num);
		},

		destroyed() {
			bus.$off('set_page_num', this.set_page_num);
		},

		methods: {
			set_page_num (page_num) {
				this.page_num = page_num;
			}
		},
	}
</script>

<style>
	.row {
		margin-left: 0px !important;
		margin-right: 0px !important;
	}

	.clickable {
		cursor: pointer;
	}
</style>